<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w w-full required">
          <label class="vs-input--label">{{ $t('BRAND_NAME') }}</label>
          <vs-input class="w-full" v-model="cardBrand.brandName"/>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col w-full">
          <label class="vs-input--label">{{ $t('EXTERNAL_ID') }}</label>
          <vs-input class="w-full" v-model="cardBrand.externalBrandId"/>
        </div>
      </div>
      <div class="nv-row">
        <div class="nv-col w-full">
          <div class="flex justify-between">
            <vs-button class="px-5 py-3" color="primary" type="border" @click="cancel">{{ $t('CANCEL') }}</vs-button>
            <div class="dropdown-button-container">
              <vs-button class="btnx" type="filled" @click="modifyCardBrand('browse')">{{ $t('SUBMIT') }}</vs-button>
              <vs-dropdown vs-trigger-click>
                <vs-button class="btn-drop" type="filled" icon="fa-angle-down" icon-pack="far"></vs-button>
                <vs-dropdown-menu>
                  <vs-dropdown-item @click="modifyCardBrand('stay')">
                    <span>{{ $t('SUBMIT_STAY') }}</span>
                  </vs-dropdown-item>
                  <vs-dropdown-item @click="modifyCardBrand('view')">
                    <span>{{ $t('SUBMIT_VIEW') }}</span>
                  </vs-dropdown-item>
                </vs-dropdown-menu>
              </vs-dropdown>
            </div>

          </div>
        </div>
      </div>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import CardBrandService from '@/services/card/CardBrandService'

export default {
  name: 'card-brand-modify',
  mixins: [AlertMixin],
  data: function () {
    return {
      cardBrand: {}
    }
  },
  methods: {
    async load () {
      let self = this
      CardBrandService.cardBrands({
        brandId: this.$route.params.id
      }).then(function (response) {
        self.cardBrand = response.data.cardBrandList[0] || {}
        self.cardBrand.disabled = self.cardBrand.disabled === 'Y'
      })
    },
    modifyCardBrand (nextAction) {
      let self = this
      CardBrandService.modifyCardBrand({
        brandId: this.$route.params.id,
        brandName: this.cardBrand.brandName,
        externalBrandId: this.cardBrand.externalBrandId
      }).then(function (response) {
        if (response.status === 200) {
          if (nextAction === 'stay') {
            self.showSuccessAlert(response.data.messages)
          } else if (nextAction === 'view') {
            self.$router.push({ name: 'ViewCardBrand', params: { id: response.data.brandId } })
          } else {
            self.$router.push({ name: 'BrowseCardBrands' })
          }
        }
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted: function () {
    this.load()
  }
}
</script>
